import styled from "styled-components";
import { BoxContentCenter } from "../Box";

export const Button = styled(BoxContentCenter).attrs({
  as: "button",
})`
  position: relative;
  overflow: hidden;

  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: rgba(${(props) => props.theme.color.ultramarineBlue}, 1);

  transition: background-color 150ms ease-in;
  cursor: pointer;

  &:hover {
    background-color: rgba(
      ${(props) => props.theme.color.ultramarineBlue},
      0.5
    );
  }

  &:active::after {
    transform: scale(1);
  }

  &::after {
    position: absolute;
    z-index: -1;

    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 1);

    transform: scale(0);
    transition: transform 150ms ease-in;
    content: "";
  }
`;
